.main-block {
  position: relative;
  background-color: gray;
  padding-top: 100vh;
  // .main-block__container

  &__title {
    color: wheat;
  }

  // .main-block__img

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  &__img img {
    width: 100%;
    height: 100vh;

    object-fit: cover;
  }

  &__pagination {
    position: absolute;
    bottom: 60px;
    z-index: 5;

    display: flex;
    justify-content: center;

    @include for-size(desktop) {
      left: 45%;
    }
  }
}

.main-swiper-pagination.section-gallery__pagination.main-block__pagination {
  position: absolute;
  bottom: 60px;
  z-index: 5;
  padding-left: 0;

  @include for-size(desktop) {
    left: 45%;
  }
}
.scrollbar-wrap {
  display: flex;
  justify-content: center;
  width: 100%;
}

.main-block__scrollbar {
  position: absolute;
  z-index: 5;
  bottom: 86px;
  width: 72px;

  @include for-size(desktop) {
    bottom: 60px;
    left: 45%;
  }
}
#main-block__slider {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
